<template>
  <div class="wallet-backups">
    	<div v-if="step==1" style="padding: 20px 10%;">
			<p style="color: #b5b5b5;">输入交易密码</p>
			<deal-password v-model="dealPassword"></deal-password>
		</div>
		
		<div v-if="step==2" style="padding: 30px;">
			<p style="line-height:1.4;padding:8px;font-size: 14px;background: #e5e5e5;color:#838383;border-radius: 5px;"> 
				注意：拥有钱包私匙就能安全控制钱包资产，因此强烈建议你在使用钱包前做好备份，将钱包私匙保存到安全的地方.建议备份完成后立即恢复一次，以确保安全。然后尝试转入，转出小额资产开始使用。
			</p>
			<mu-button @click="step=3" full-width style="height: 44px;margin-top: 30px;" color="#295DF1">立即备份</mu-button>
		</div>
		
		<div v-if="step==3">
			<p style="font-size: 12px;color: #a5a5a5;background: #e5e5e5;padding:10px 20px;margin-top: 0;">钱包助记词用于恢复钱包资产，拥有助记词即可完全控制钱包资产，请务必妥善保管，丢失助记词即丢失钱包资产，无法提供找回功能。</p>
			<div style="margin: 40px;text-align: center;">
				<p>请记下你的钱包助记词并保存到安全的地方</p>
				<mu-text-field v-model="memorizing" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="6" full-width></mu-text-field><br/>
				<mu-button @click="step=4" class="creatBtn" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">下一步</mu-button>
			</div>
		</div>	
		
		<div v-if="step==4">
			<p style="margin: 30px auto;text-align: center;">验证你的钱包助记词</p>
			<div style="padding: 0 30px;">
				<p style="margin:10px 0;color: #a5a5a5;">请根据你记下的助记词,按顺序点击,验证你备份的助记词正确无误</p>
				<mu-text-field readonly v-model="memorizingWords" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="4" full-width></mu-text-field><br/>
				
				<div>
					<div style="margin:10px 0;">
						<mu-button v-for="item,index in newBackups" :key="index" @click="choose(item.name,item.isChoose)" :style="item.isChoose?'border: 1px solid #295DF1;margin: 5px;':'color:#295DF1;margin: 5px'" small :color="item.isChoose?'#295DF1':'#fff'">{{item.name}}</mu-button>
					</div>
				</div>
				
				<mu-button @click="stepFive" class="creatBtn" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">下一步</mu-button>
			</div>
		</div>	
		<div v-if="step==5">
			<div style="margin:40px;text-align: center;">
			<i class="iconfont" style="font-size:100px;color: #295DF1;margin-top: 30px;">&#xe669;</i>
			<p>钱包备份成功!</p>
			<mu-button class="creatBtn" @click="entryWallet" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">进入钱包</mu-button>
		</div>
		</div>	
		<!--<div v-if="activeStep==1" style="padding: 20px 10%;">
			<p style="color: #b5b5b5;">设置钱包名称</p>
			<input placeholder="请输入钱包名称" v-model="walletName" style="padding-left: 10px;height: 47px;width: 100%;border: 1px solid #e5e5e5;background-color: transparent;"/>
			<mu-button full-width style="height: 44px;margin-top: 30px;" @click="oneStep" color="#295DF1">下一步</mu-button>
		</div>
		
		<div v-if="activeStep==3" style="padding: 20px 10%;">
			<div style="text-align: center;margin-top: 20px;">
				<i class="iconfont" style="font-size:60px;color: #295DF1;">&#xe669;</i>
				<p style="margin: 1px 0">钱包创建成功 !</p>
			</div>
			<p style="line-height:1.4;padding:8px;font-size: 14px;background: #e5e5e5;color:#838383;border-radius: 5px;"> 
				注意：拥有钱包私匙就能安全控制钱包资产，因此强烈建议你在使用钱包前做好备份，将钱包私匙保存到安全的地方.建议备份完成后立即恢复一次，以确保安全。然后尝试转入，转出小额资产开始使用。
			</p>
			<router-link to="/home"><mu-button full-width style="height: 44px;margin-top: 30px;" color="#295DF1">进入钱包</mu-button></router-link>
		</div>-->
		</div>
  </div>
</template>
<script>
import DealPassword from '@/components/dealPassword'
export default {
  name: "WalletBackups",
  components:{
  	'deal-password':DealPassword
  },
  data() {
    return {
      step:1,
      memorizing:'',
      memorizingWords:'',
      dealPassword:'',
      chooseArr:[],
      backups:[],
    	newBackups:[],
    	walletInfo:{}
    };
  },
  created(){
  	this.walletInfo = JSON.parse(this.$route.query.details);
  	this.memorizing = this.walletInfo.secretSeed;
  	this.backups = this.walletInfo.secretSeed.split(' ')
  	for(let i in this.backups){
	     this.newBackups.push({
	     		id:1,
	     		name:this.backups[i],
	     		isChoose:false
	     })
    }
  },
  methods: {
		//第一步
		oneStep(){
			
		},
		//第二步
		twoStep(){
			
		},
		//第五步-验证助记词
		stepFive(){
			if(this.memorizingWords == this.memorizing){
				this.step=5;
			}else{
				this.$layer.open({
					content: '助记词验证错误'
				    ,skin: 'msg'
				    ,time: 2 //2秒后自动关闭
				})
			}
		},
		choose(name,isChoose){
			let index = this.chooseArr.indexOf(name);
			if(!isChoose){
				this.chooseArr.push(name);
				this.memorizingWords=this.chooseArr.toString().replace(/,/g,' ');
				for(let i in this.newBackups){
			     if(this.newBackups[i].name == name){
			     	this.$set(this.newBackups[i],'isChoose',true)
			     }
		    }
			}else{
				this.chooseArr.splice(index,1);
				this.memorizingWords=this.chooseArr.toString().replace(/,/g,' ');
				for(let i in this.newBackups){
			     if(this.newBackups[i].name == name){
			     	this.$set(this.newBackups[i],'isChoose',false)
			     }
		    }
			}
		},
		entryWallet(){
			this.$router.replace('/myWallet');
			this.$router.go(-1);
		},
		//打乱数组
		shuffle(arr) {
	    let i = arr.length;
	    while (i) {
	        let j = Math.floor(Math.random() * i--);
	        [arr[j], arr[i]] = [arr[i], arr[j]];
	    }
	    return arr;
		}
  },
  
  computed: {
    
  },
  watch:{
  	dealPassword(newV,oldV){
	  	if(newV.length == 6) {
				if(this.walletInfo.pwd == newV) {
					this.step=2;
				} else {
					this.$layer.open({
						content: '密码错误',
						skin: 'msg',
						time: 2 //2秒后自动关闭
					})
				this.dealPassword=''
				}
			}
  	},
  	step(newV,oldV){
  		if(newV==4){
  			this.newBackups=[];
  			this.backups = this.walletInfo.secretSeed.split(' ')
  			this.shuffle(this.backups);
		  	for(let i in this.backups){
			     this.newBackups.push({
			     		id:1,
			     		name:this.backups[i],
			     		isChoose:false
			     })
		    }
  		}
  	}
  }
};
</script>
<style scoped>
	
</style>




